import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HappyTradingComponent } from './happy-trading.component';

const routes: Routes = [
    {
        path: '',
        component: HappyTradingComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class HappyTradingRoutingModule {
}
